"use client";
import { useState } from "react";
import { Card } from "@/components/ui/card";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { ChevronDown, ChevronUp } from "lucide-react";
import { Button } from "@/components/ui/button";

export default function Statistics() {
  const [view, setView] = useState("week");
  const [dropdownOpen, setDropdownOpen] = useState(false);

  const data = [
    { day: "Sun", thisWeek: 240000, lastWeek: 180000 },
    { day: "Mon", thisWeek: 180000, lastWeek: 220000 },
    { day: "Tue", thisWeek: 200000, lastWeek: 170000 },
    { day: "Wed", thisWeek: 220000, lastWeek: 200000 },
    { day: "Thu", thisWeek: 260000, lastWeek: 230000 },
    { day: "Fri", thisWeek: 240000, lastWeek: 190000 },
    { day: "Sat", thisWeek: 210000, lastWeek: 170000 },
  ];

  return (
    <div className="w-full">
      {/* Statistics Heading outside card */}
      {/* <h1 className="text-gray-400 text-base font-medium mb-2 text-right sm:pr-4">
        Statistics
      </h1> */}

      {/* Chart Card */}
      
      <Card
  className="
    p-5 rounded-lg shadow-sm bg-white 
    w-full h-[250px] mt-4
    lg:max-w-[680px] lg:h-[250px] 
    lg:ml-auto lg:mr-[-0px] lg:mt-[-525px]
    
  "
>



        {/* Header inside card */}
        <div className="flex justify-between items-center mb-4 relative">
          {/* Dropdown */}
          <div
            className="flex items-center gap-1 cursor-pointer"
            onClick={() => setDropdownOpen(!dropdownOpen)}
          >
            <h2 className="text-sm font-semibold text-gray-600">
              {view === "month" ? "Monthly Comparison" : "Weekly Comparison"}
            </h2>
            {dropdownOpen ? (
              <ChevronUp size={16} className="text-gray-500" />
            ) : (
              <ChevronDown size={16} className="text-gray-500" />
            )}
          </div>

          {/* Buttons inside card */}
          <div className="flex flex-wrap gap-2 justify-end">
  <Button
    variant={view === "week" ? "default" : "outline"}
    size="sm"
    className={`text-xs px-2 py-1 ${
      view === "week"
        ? "bg-[#0d9488] text-white"
        : "border-green-600 text-green-600"
    }`}
    onClick={() => setView("week")}
  >
    This week
  </Button>
  <Button
    variant={view === "last" ? "default" : "outline"}
    size="sm"
    className={`text-xs px-2 py-1 ${
      view === "last"
        ? "bg-[#0d9488] text-white"
        : "border-green-600 text-green-600"
    }`}
    onClick={() => setView("last")}
  >
    Last week
  </Button>
</div>


          {/* Dropdown Menu */}
          {dropdownOpen && (
            <div className="absolute top-7 left-0 bg-white shadow-md rounded-md border w-40 text-sm z-10">
              <div
                className="px-3 py-2 hover:bg-gray-100 cursor-pointer"
                onClick={() => {
                  setView("week");
                  setDropdownOpen(false);
                }}
              >
                Weekly Comparison
              </div>
              <div
                className="px-3 py-2 hover:bg-gray-100 cursor-pointer"
                onClick={() => {
                  setView("month");
                  setDropdownOpen(false);
                }}
              >
                Monthly Comparison
              </div>
            </div>
          )}
        </div>

        {/* Chart */}
        <ResponsiveContainer width="100%" height="75%">
          <BarChart
            data={data}
            margin={{ top: 0, right: 20, left: 40, bottom: 0 }}
          >
            <CartesianGrid strokeDasharray="3 3" vertical={false} />
            <XAxis dataKey="day" tick={{ fontSize: 12 }} axisLine={false} />
            <YAxis
              tick={{ fontSize: 12 }}
              tickFormatter={(v) => `$${(v / 1000).toFixed(0)}k`}
              axisLine={false}
            />
            <Tooltip
              formatter={(value) => `$${value.toLocaleString()}`}
              contentStyle={{ fontSize: "12px" }}
            />
            <Bar
              dataKey="lastWeek"
              fill="#e5e7ed"
              radius={[6, 6, 0, 0]}
              barSize={15}
            />
            <Bar
              dataKey="thisWeek"
              fill="#0d9488"
              radius={[6, 6, 0, 0]}
              barSize={15}
            />
          </BarChart>
        </ResponsiveContainer>
      </Card>
    </div>
  );
}
