// src/components/dashboard/RecentTransactions.jsx
import { ShoppingCart, Shirt, Utensils, Car, Keyboard, Coffee, Laptop, Gift } from "lucide-react";

const transactions = [
  { id: 1, title: "GTR 5", subtitle: "Gadget & Gear", amount: 160, date: "17 May 2025", icon: ShoppingCart },
  { id: 2, title: "Polo Shirt", subtitle: "XL fashions", amount: 20, date: "17 May 2025", icon: Shirt },
  { id: 3, title: "Biriyani", subtitle: "Hajir Biriyani", amount: 10, date: "17 May 2025", icon: Utensils },
  { id: 4, title: "Taxi Fare", subtitle: "Uber", amount: 12, date: "17 May 2025", icon: Car },
  { id: 5, title: "Keyboard", subtitle: "Gadget & Gear", amount: 22, date: "17 May 2025", icon: Keyboard },
  { id: 6, title: "Coffee", subtitle: "Starbucks", amount: 5, date: "18 May 2025", icon: Coffee },
  { id: 7, title: "MacBook", subtitle: "Apple Store", amount: 1200, date: "18 May 2025", icon: Laptop },
//   { id: 8, title: "Gift", subtitle: "Amazon", amount: 50, date: "18 May 2025", icon: Gift },
];

export default function RecentTransactions() {
  return (
    <div className="w-full max-w-[320px]">
      {/* Section Header */}
      <div className="flex items-center justify-between mb-3 px-1">
        <h2 className="text-22px font-inter text-gray-500 mb-0">
          Recent Transactions
        </h2>
        <button className="text-sm text-teal-600 font-medium hover:underline">
          View All
        </button>
      </div>

      {/* Card Box */}
<div className="bg-white rounded-2xl shadow-sm p-4">
        {/* Tabs */}
        <div className="flex gap-6 text-gray-600 font-medium mb-4 text-sm">
          <span className="text-teal-600 border-b-2 border-teal-600 pb-1">All</span>
          <span className="hover:text-black cursor-pointer">Revenue</span>
          <span className="hover:text-black cursor-pointer">Expenses</span>
        </div>

        {/* Transactions List */}
        <div className="divide-y divide-gray-200">
          {transactions.map((tx) => {
            const Icon = tx.icon;
            return (
              <div
                key={tx.id}
                className="flex items-center justify-between py-3"
              >
                {/* Icon + Text */}
                <div className="flex items-center gap-3">
                  <div className="w-9 h-9 flex items-center justify-center rounded-full bg-gray-100 text-teal-600">
                    <Icon className="w-5 h-5" />
                  </div>
                  <div>
                    <p className="font-medium text-sm">{tx.title}</p>
                    <p className="text-xs text-gray-500">{tx.subtitle}</p>
                  </div>
                </div>

                {/* Amount + Date */}
                <div className="text-right">
                  <p className="font-semibold text-sm">${tx.amount.toFixed(2)}</p>
                  <p className="text-xs text-gray-400">{tx.date}</p>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
